/* major breakpoints */
@custom-media --sp (max-width: 767px);
@custom-media --tb (min-width: 768px) and (max-width: 1023px);
@custom-media --tb-h (min-width: 1024px) and (max-width: 1279px);
@custom-media --not-sp (min-width: 768px);
:root {
  --app-height: 100%;
}

.flow {
  background-color: #F7F7FA;
}
@media screen and (max-width: 768px) {
  .flow .card:not(:first-of-type) {
    margin-top: 64px;
  }
}
.flow .num {
  color: #E01A12;
  font-family: "Fragment Mono", monospace;
}
.flow .title {
  color: #16161A;
}
.flow .text {
  color: #7A7676;
}

.main {
  background: none;
}
.main .overview {
  background-color: #FFFFFF;
}
@media screen and (max-width: 768px) {
  .main .overview {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
.main .overview .lead {
  color: #16161A;
  font-family: "IBM Plex Sans JP", sans-serif;
  font-size: 40px;
  font-weight: 700;
}
@media screen and (max-width: 768px) {
  .main .overview .lead {
    font-size: 36px;
  }
}
.main .overview .text {
  color: #7A7676;
  font-family: "IBM Plex Sans JP", sans-serif;
  font-size: 22px;
  font-weight: 500;
}
@media screen and (max-width: 768px) {
  .main .overview .text {
    font-size: 22px;
  }
}
@media screen and (max-width: 768px) {
  .main .overview.overview2 {
    justify-content: center;
  }
}

.background-image picture {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
}
.background-image img {
  width: 100vw;
  height: 100%;
  object-fit: cover;
}